/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center; }

@keyframes bouncing-loader {
  to {
    opacity: 0.1;
    transform: translate3d(0, -1rem, 0); } }

.bouncing-loader {
  display: flex;
  justify-content: center; }
  .bouncing-loader > div {
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #8385aa;
    border-radius: 50%;
    animation: bouncing-loader 0.6s infinite alternate; }
    .bouncing-loader > div:nth-child(2) {
      animation-delay: 0.2s; }
    .bouncing-loader > div:nth-child(3) {
      animation-delay: 0.4s; }

/* img */
.img-responsive, .img--responsive {
  display: block;
  height: auto;
  max-width: 100%; }

.img-round, .img--round {
  border-radius: 50%; }

/* section */
.section {
  padding: 50px 0;
  min-width: 320px; }

.container .section {
  min-width: inherit; }

ul, ol {
  padding-left: 25px;
  margin-bottom: 35px; }
  ul li, ol li {
    list-style: none;
    position: relative; }
    ul li::before, ul li > span.span, ol li::before, ol li > span.span {
      content: '';
      position: absolute;
      top: 6px;
      left: 0;
      margin-left: -25px;
      font-size: 12px;
      font-weight: bold;
      color: #FF9550;
      line-height: 1; }
  @media (min-width: 1300px) {
    ul, ol {
      padding-left: 65px; }
      ul li::before, ul li > span.span, ol li::before, ol li > span.span {
        top: 10px; } }

ul li::before {
  width: 10px;
  height: 10px;
  background: linear-gradient(180deg, #FFB950 0%, #FF9550 100%);
  box-shadow: 5px 15px 30px rgba(255, 149, 80, 0.5);
  border-radius: 50%; }

ol li::before {
  display: none; }

